import React from "react";
import PlanBlock from "./PlanBlock";
import Flickity from "react-flickity-component";
const flickityOptions = {
  draggable: true,
  // freeScroll: true,
  // contain: true,
  // // disable previous & next buttons and dots
  prevNextButtons: false,
  // pageDots: false,
  // groupCells: 2,
  // wrapAround:true,
  cellAlign: 'left'
};
const data = [
  {
    title: " ",
    speed: " 40 /",
    describe:
      "   ,         .",
    price: 399,
  },
  {
    title: " ",
    speed: "80-100 /",
    describe:
      "    .         !",
    price: 449,
  },
  {
    title: " ",
    speed: " 40 /",
    describe:
      "   ,         .",
    price: 449,
  },
];
const Plans: React.FC = () => {
  return (
    <div className="plans">
      <h2> </h2>

      <Flickity
        className={"items"} 
        elementType={"div"}
        options={flickityOptions}
      >
        {data.map((item, index) => {
          return (
            <PlanBlock
              key={index}
              title={item.title}
              speed={item.speed}
              describe={item.describe}
              price={item.price}
            />
          );
        })}
      </Flickity>
    </div>
  );
};

export default Plans;
