import React from "react";
type PlanBlockProps = {
  title: string;
  speed: string;
  price: number;

  describe: string;
};
const PlanBlock: React.FC<PlanBlockProps> = ({
  title,
  speed,
  describe,
  price,
}) => {
  const [select, setSelect] = React.useState(true);
  const onClickButton = () => {
    setSelect(!select);
  };

  return (
    <div className="plan__block">
      <div className="info">
        <h2>{title}</h2>
        <p className="speed"> </p>
        <span>{speed}</span>
        <p className="describe">{describe}</p>
        <form>
          <input type="checkbox" className="checkbox"></input>
          <label> + 100  ( )</label>
        </form>
        <p className="price">
          {price} <span> </span>
        </p>
        <p className="notice">
          *         
          
        </p>
      </div>

      {/* <Button title={" "} />
       */}
      <button
        onClick={onClickButton}
        className={`button ${select ? "pink" : "blue"}`}
      >
        {`${select ? " " : " "}`}
      </button>
    </div>
  );
};

export default PlanBlock;
